* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-section {
  background-color: #806fe6;
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  width: 350px;
  border: 1px solid #ccc;
  border-right-style: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 10px;
}

.search-btn {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 0 4px 4px 0;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 5.5px;
  display: flex;
}

.gallery-section {
  padding: 25px;
}

.gallery-list {
  flex-wrap: wrap;
  gap: 10px;
  margin: 0;
  padding: 0;
  display: flex;
}

.list-item {
  width: calc(25% - 15px);
  display: block;
}

.photo-container {
  height: 180px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  height: 200px;
  width: 300px;
  padding: 20px;
  display: block;
}

.info {
  align-items: center;
  gap: 10px;
  padding-top: 10px;
  font-size: 14px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.load-more__is-hidden {
  display: none;
}

.load-more {
  color: #000;
  width: 150px;
  height: 50px;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
  top: 50px;
}

